.hero {
  color: #fff;
  cursor: pointer;
}

.hero-row {
  margin-top: 130px !important;
}

.hero-content {
  transform: scale(1.2);
}

.profile {
  display: block;
  width: 75%;
  margin: 0 auto;
  border-radius: 15px;
}

.hero .name {
  font-size: 4rem;
}

.hero .design,
.develope,
.test {
  text-decoration: underline;
  text-decoration-color: #F6BA08;
  transition: 1s;
}

.hero .design:hover,
.develope:hover,
.test:hover {
  background: #F6BA08;
}

.hero .contact-me {
  border: #fff solid 2px;
  width: 120px;
  background: none !important;
  padding: 5px;
  text-align: center;
  font-weight: 600;
}

.hero .contact-me:hover {
  background: #fff !important;
}

.hero .contact-me:hover a {
  color: #181a51;
}

.contact-me a {
  text-decoration: none;
  color: #fff;
}

.hero .resume {
  border: #fff solid 2px;
  width: 120px;
  background: #fff !important;
  padding: 5px;
  text-align: center;
  font-weight: 600;
}

.hero .resume:hover {
  background: none !important;
}

.hero .resume:hover a {
  color: #fff;
}

.resume a {
  text-decoration: none;
  color: #181a51;
}

@media only screen and (max-width: 576px) {
  .hero-row {
    margin-top: 60px !important;
  }

  .profile {
    width: 70%;
  }

  .hero .name {
    font-size: 3rem;
    margin-top: 10px;
  }

  .hero-content {
    margin: 20px 0 !important;
    transform: scale(1);
  }

  .hero .contact-me {
    width: 100px;
  }

  .hero .resume {
    width: 100px;
  }
}
