@import url(https://fonts.googleapis.com/css2?family=Pompiere&display=swap);
body {
  font-family: 'Pompiere', cursive !important;
  letter-spacing: 1.5px;
}

body {
  /* min-height: 100vh; */
  /* background: rgb(16, 15, 15) !important; */
  background-image: linear-gradient(90deg, #020024 0%, #090979 35%, #00d4ff 100%);
}


.about {
  min-height: 100vh;
}

.about-row {
  margin-top: 30px !important;
}

.about-me {
  color: #fff;
  font-size: 1.3rem;
}

.info-card {
  color: #fff;
  font-size: 1.3rem;
}

.info-card span {
  color: #fff;
  font-weight: 600;
  margin-top: -10px !important;
}

.skill-card {
  background: none !important;
  cursor: pointer;
  color: #fff;
  border: solid 1px rgba(255, 255, 255, 0.103) !important;
  border-radius: 0 !important;
}

.skill-card:hover {
  color: #fff !important;
  border-radius: 0 !important;
}

.skill-card .name {
  font-size: 2rem;
  font-weight: 600;
}

.skill-card .percent {
  font-size: 1.5rem;
  font-weight: 600;
  display: none;
}

.skill-card .icon {
  font-size: 5rem;
  margin-top: -20px;
}

.skill-card:hover .name {
  display: none;
}

.skill-card:hover .percent {
  display: block;
}

.skill-card:hover .icon {
  margin-bottom: 0 !important;
}

.about-underline {
  height: 2px;
  width: 10% !important;
  background: #f9bd09;
}

.cv {
  text-decoration: none !important;
  border: #e13d2e3d solid 2px;
  display: block;
  width: 20%;
  text-align: center;
  margin-top: 20px !important;
  color: #fff;
}

.cv:hover {
  background: #e13d2e5b;
  color: #fff;
}

@media only screen and (max-width: 576px) {
  .info-card {
    font-size: 1.1rem;
  }

  .cv {
    margin-top: 20px !important;
    width: 30%;
  }

  .about-row {
    margin-top: 20px !important;
  }

  .tech {
    margin-top: 30px !important;
  }

  .about-me {
    text-align: justify;
    font-size: 1.1rem;
  }
}

.header {
  color: #fff;
  position: relative;
  background-image: linear-gradient(90deg, #020024 0%, #090979 35%, #00d4ff 100%);
}

.header .title {
  font-weight: 600;
}

.header .title:hover {
  color: #fff;
}

.header a {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
}

.header a:hover {
  color: #F6BA08;
}

.main-nav {
  display: flex;
}

.mobile-nav-container {
  position: fixed;
  top: 0 !important;
  left: 0 !important;
  min-height: 100vh;
  width: 50%;
  background: #e13d2e5b;
  z-index: 999;
  display: none;
  transition: 1s;
}

.mobile-nav {
  display: grid;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.mobile-nav a:hover {
  color: #fff;
  text-decoration: underline !important;
}

.close-menu {
  cursor: pointer;
  position: absolute;
  top: 15px;
  left: 15px;
  color: #fff;
  font-size: 1.4rem;
}

.mobile-btn {
  display: none;
  cursor: pointer;
  font-size: 1.4rem;
}

.blur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  z-index: 888;
  display: none;
}

.show {
  display: block;
}

@media only screen and (max-width: 576px) {
  header {
    border-bottom: solid 1px rgba(255, 255, 255, 0.103) !important;
  }
  
  .mobile-btn {
    display: block;
  }

  .main-nav {
    display: none !important;
  }

  .mobile-nav a {
    font-size: 1.5rem;
  }
}

.TITLE {
  margin-top: 20px;
}

.title-line {
  height: 2px;
  width: 5% !important;
  margin: 0 auto;
  background: #fff;
}
.footer-line {
  height: 1px;
  width: 75%;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.103);
  border-radius: 40px;
}

.footer-content {
  display: flex;
  align-items: center;
  width: 75%;
  margin: auto;
  justify-content: space-between;
}

.footer-icon {
  margin-top: -10px !important;
  color: #f9bd09;
}

@media only screen and (max-width: 576px) {
  .footer-content {
    width: 100%;
    justify-content: center;
  }

  .rights {
    display: none;
  }
}

.contact-card {
  border: solid 1px rgba(255, 255, 255, 0.103) !important;
  background: none !important;
  border-radius: 0 !important;
  cursor: pointer;
}

.contact-row {
  margin-top: 30px !important;
}

.contact-card p {
  font-size: 1.2rem;
}

.social-icon {
  font-size: 2rem;
  margin-top: -10px;
}

.social-container {
  height: 50px;
  width: 50px;
  border: solid rgba(255, 255, 255, 0.103) 1px;
  display: grid;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.social-container:hover {
  background: rgba(255, 255, 255, 0.103) !important;
}

.services-card {
  display: flex;
  align-items: center;
  background: none !important;
  border: solid 1px rgba(255, 255, 255, 0.103) !important;
  cursor: pointer;
  font-size: 1.1rem;
}

.number {
  background: #e13d2e5b;
  font-size: 4rem;
  text-align: center;
  -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
          clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
}



@media only screen and (max-width: 576px) {
  .contact-card  {
    margin: 0 auto;
  }

  .contact-card p {
    font-size: 1.2rem;
  }

  .social-icon {
    font-size: 1.5rem;
    margin-top: -10px;
  }

  .social-container {
    height: 40px;
    width: 40px;
    border-radius: 20px;
  }

  .contact-row {
    margin-top: 20px !important;
  }
}

.hero {
  color: #fff;
  cursor: pointer;
}

.hero-row {
  margin-top: 130px !important;
}

.hero-content {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.profile {
  display: block;
  width: 75%;
  margin: 0 auto;
  border-radius: 15px;
}

.hero .name {
  font-size: 4rem;
}

.hero .design,
.develope,
.test {
  text-decoration: underline;
  -webkit-text-decoration-color: #F6BA08;
          text-decoration-color: #F6BA08;
  transition: 1s;
}

.hero .design:hover,
.develope:hover,
.test:hover {
  background: #F6BA08;
}

.hero .contact-me {
  border: #fff solid 2px;
  width: 120px;
  background: none !important;
  padding: 5px;
  text-align: center;
  font-weight: 600;
}

.hero .contact-me:hover {
  background: #fff !important;
}

.hero .contact-me:hover a {
  color: #181a51;
}

.contact-me a {
  text-decoration: none;
  color: #fff;
}

.hero .resume {
  border: #fff solid 2px;
  width: 120px;
  background: #fff !important;
  padding: 5px;
  text-align: center;
  font-weight: 600;
}

.hero .resume:hover {
  background: none !important;
}

.hero .resume:hover a {
  color: #fff;
}

.resume a {
  text-decoration: none;
  color: #181a51;
}

@media only screen and (max-width: 576px) {
  .hero-row {
    margin-top: 60px !important;
  }

  .profile {
    width: 70%;
  }

  .hero .name {
    font-size: 3rem;
    margin-top: 10px;
  }

  .hero-content {
    margin: 20px 0 !important;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  .hero .contact-me {
    width: 100px;
  }

  .hero .resume {
    width: 100px;
  }
}

.project-img {
  width: 100%;
}

.project-card {
  border-radius: 0 !important;
  border: solid 1px rgba(255, 255, 255, 0.103) !important;
  background: none !important;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  cursor: pointer;
  height: 220px !important;
  overflow: hidden;
  width: 50% !important;
  padding: 3px;
}

.portfolio-row {
  margin-top: 30px !important;
  color: #fff;
}

.projects-data {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.visit {
  width: 50% !important;
}

.visit a {
  text-decoration: none !important;
  border: solid 1px rgba(255, 255, 255, 0.103);
  margin: 5px 0;
  padding: 3px;
  font-weight: 600;
  text-align: center;
  display: block;
  width: 80px !important;
  color: #fff;
  cursor: pointer;
  border-radius: 20px;
}

.visit a:hover {
  background: rgba(255, 255, 255, 0.103);
}



