.project-img {
  width: 100%;
}

.project-card {
  border-radius: 0 !important;
  border: solid 1px rgba(255, 255, 255, 0.103) !important;
  background: none !important;
  transform: scale(0.9);
  cursor: pointer;
  height: 220px !important;
  overflow: hidden;
  width: 50% !important;
  padding: 3px;
}

.portfolio-row {
  margin-top: 30px !important;
  color: #fff;
}

.projects-data {
  display: flex;
  align-items: center;
  gap: 10px;
}

.visit {
  width: 50% !important;
}

.visit a {
  text-decoration: none !important;
  border: solid 1px rgba(255, 255, 255, 0.103);
  margin: 5px 0;
  padding: 3px;
  font-weight: 600;
  text-align: center;
  display: block;
  width: 80px !important;
  color: #fff;
  cursor: pointer;
  border-radius: 20px;
}

.visit a:hover {
  background: rgba(255, 255, 255, 0.103);
}


